import Upload from "./Upload";

export default {
    name: "Avatar",
    components: {
        Upload
    },
    props: {
        canUpdate: {
            type: Boolean,
            default: false
        },
        value: {
            default: null,
            type: Object
        },
        name: {
            default: null
        },
        icon: {
            default: 'image-frame',
            type: String
        },
        size: {
            default: 'thumb_24_24'
        }
    },
    data() {
        return {
            data: this.value,
            isUpdating: false
        }
    },
    methods: {
        handleUpload(files) {
            if (files.length) {
                this.data = files[0]
                this.isUpdating = false
            }
        },
        cleanURI(url) {
            if (url) {
                return url.split("?")[0].split("#")[0];
            }
            return null
        },
        avatarName(name, length) {
            let matches = name.match(/\b(\w)/g);
            if (length) {
                return matches.join('').substr(0, length)
            }
            return matches.join('')
        }
    },
    watch: {
        data() {
            this.$emit('input', this.data)
        },
        value() {
            this.data = this.value
        }
    }
}
