export default {
    name: "CommentForm",
    props: {
        ui: {
            default: null
        },
        parent: {
            default: null
        }
    },
    data() {
        return {
            comment: null
        }
    },
    methods: {
        submit() {
            this.$api['comment'].post({
                ui: this.ui.id,
                content: this.comment,
                parent: this.parent ? this.parent.id : undefined
            }).then(res => {
                this.$emit('posted');
                this.comment = null;
            })
        }
    }
}
