import {debounce} from "lodash";

export default {
    name: "TagSelect",
    data() {
        return {
            response: {
                results: [],
                count: 0
            },
        }
    },
    methods: {
        async fetch(text) {
            this.response = await this.$api.hash_tag.list({
                page_size: 5,
                search: text
            });
        },
        delay_search: debounce(function (text) {
            this.fetch(text)
        }, 800)
    },
    computed: {
        app() {
            return this.$store.state.config.app;
        }
    },
    created() {
        this.fetch();
    }
}
