const SAMPLE = {
    first_name: "",
    last_name: "",
    username: "anonymous",
    profile: {media: null, description: null, nick: "Anonymous"},
    is_staff: false
};

export default {
    name: "User",
    props: {
        canFollow: {
            default: false,
            type: Boolean
        },
        value: {
            default: () => SAMPLE
        },
        username: {
            default: false,
            type: Boolean
        },
        avatarSize: {
            default: 'is-32x32',
            type: String
        },
        onlyAvatar: {
            default: false,
            type: Boolean
        },
        clickable: {
            default: true,
            type: Boolean
        },
        following: {
            default: false,
            type: Boolean
        }
    },
    methods: {
        click() {
            if (!this.canFollow && this.value.id && this.clickable) {
                this.$router.replace({path: `/profile/${this.value.username}`})
            }
        }
    }
}
