export default {
    name: "Login",
    props: {
        login: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            isActive: false,
            form: {
                first_name: null,
                last_name: null,
                username: null,
                email: null,
                name: null,
                password: null,
                password1: null,
                password2: null,
            },
            validForm: {},
            loginTemp: this.login
        }
    },
    methods: {
        async handleSubmit() {
            if (this.loginTemp) {
                let res = await this.$auth.login({
                    email: this.form.email,
                    password: this.form.password
                }).then(res => {
                    this.$emit('success')
                }).catch(err => {
                    console.log(err);
                })
            } else {
                await this.$axios.$post('/auth/rest-auth/registration/', this.form).then(res => {
                    this.loginTemp = true
                }).catch(err => {
                    console.log(err);
                })
            }
        }
    }
}
