export default {
    name: "Comment",
    props: {
        value: {
            default: null
        },
        ui: {
            default: null
        },
        avatarSize: {
            default: 'is-32x32'
        }
    },
    data() {
        return {
            response: {
                results: [],
                count: 0
            }
        }
    },
    methods: {
        reply(comment) {
            this.$emit('reply', comment);
        },
        async fetchChild() {
            if (this.value['has_child']) {
                this.response = await this.$api['comment'].list({
                    ui: this.ui.id,
                    parent: this.value.id
                })
            }
        }
    },
    created() {
        this.fetchChild();
    }
}
